const codeString = `
/*
  Method 1
  Time complexity: O()
  Space complexity: O()
*/
class ListNode {
    val: number;
    next: ListNode | null;

    constructor(val: number, next: ListNode | null = null) {
        this.val = val;
        this.next = next;
    }
}

class Solution {
    addTwoNumbers(l1: ListNode | null, l2: ListNode | null): ListNode | null {
        let dummyHead = new ListNode(0);
        let p: ListNode | null = l1, q: ListNode | null = l2, curr: ListNode = dummyHead;
        let carry: number = 0;

        while (p !== null || q !== null) {
            let x: number = (p !== null) ? p.val : 0;
            let y: number = (q !== null) ? q.val : 0;
            let sum: number = carry + x + y;
            carry = Math.floor(sum / 10);
            curr.next = new ListNode(sum % 10);
            curr = curr.next;

            if (p !== null) p = p.next;
            if (q !== null) q = q.next;
        }

        if (carry > 0) {
            curr.next = new ListNode(carry);
        }

        return dummyHead.next;
    }
}
class Listnode {
    val: number;
    next: Listnode | null;

    constructor(val: number, next: Listnode | null = null) {
        this.val = val;
        this.next = next;
    }

    const printListNode = (ListNode: Listnode) => {
        while(ListNode.val !== null) {
            console.log(ListNode.val);
            ListNode.next;
            console.log(ListNode.val);
        }

    }
 }


const dummyHead = new Listnode(0);
dummyHead.printListNode();




`;

